.wrapper {
  width: 1120px;
  height: 577px;
  margin: 0 auto;
  transform: translateY(calc(50vh - (577px / 2)));
}

.header {
  font-family: "Exo2";
  font-size: 36px;
  line-height: 44px;
  text-align: center;
  color: #ffffff;
  text-shadow: 0px 1px 1px #000000;
}

.foodList {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

@media (max-width: 1130px) {
  .wrapper {
    width: 700px;
  }
}

@media (max-width: 700px) {
  .wrapper {
    width: 320px;
  }
}
