.FoodItem {
  width: 320px;
  margin-bottom: 30px;
}

.FoodItem:not(:nth-child(3)) {
  margin-right: 80px;
}

a {
  text-decoration: none;
  border-bottom: 1px dashed #1698d9;
  color: #1698d9;
}

.cardBorder {
  position: relative;
  width: 320px;
  height: 480px;
  padding: 21px 0 0 51px;
  border-radius: 11px;
  background: linear-gradient(135deg, transparent 31px, #1698d9 0), 0 0,
    no-repeat;
  overflow: hidden;
  z-index: -2;
}

.cardBorder::after {
  content: "";
  position: absolute;
  top: 4px;
  right: 4px;
  bottom: 4px;
  left: 4px;
  border-radius: 8px;
  background: linear-gradient(135deg, transparent 32px, #f2f2f2 0) top left;
  overflow: hidden;
  z-index: -1;
}

.wrapperContent {
  position: absolute;
  top: 4px;
  left: 4px;
  right: 4px;
  bottom: 4px;
  border-radius: 10px;
  padding: 17px 0 0 48px;
  overflow: hidden;
}

.topTitle {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #666666;
}

.topTitleActive {
  color: #e62e7a;
}

.productName {
  margin: 5px 0 0 -3px;
  font-style: normal;
  font-weight: 700;
  font-size: 48px;
  line-height: 56px;
  color: #000000;
}

.taste {
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: #000000;
}

.portions {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #666666;
}

.topPortions {
  margin: 14px 0 0 0px;
  letter-spacing: 0.1px;
}

.botPortions {
  letter-spacing: -0.05px;
}

.botPortions span,
.topPortions span {
  font-weight: 700;
}

.photo {
  position: absolute;
  bottom: -4px;
  left: -4px;
}

.photoBlocked {
  opacity: 0.5;
}

.weightWrapper {
  position: absolute;
  bottom: 12px;
  right: 12px;
  display: flex;
  flex-direction: column;
  width: 80px;
  height: 80px;
  background: #2ea8e6;
  border-radius: 50%;
}

.weightCount {
  margin: 20px 0 0 0;
  font-style: normal;
  font-weight: 400;
  font-size: 42px;
  line-height: 22px;
  text-align: center;
  color: #ffffff;
}

.weight {
  margin-top: 6px;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 22px;
  text-align: center;
  color: #ffffff;
}

.weightBlocked {
  background: #b3b3b3;
}

.bottomText {
  margin-top: 14px;
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  color: #ffffff;
}

.isSelected {
  background: linear-gradient(135deg, transparent 31px, #d91667 0), 0 0,
    no-repeat;
}

.bottomBlocked {
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
  text-align: center;
  color: #ffff66;
  letter-spacing: -0.1px;
  word-spacing: 0.5px;
}

.textBlocked {
  color: #b3b3b3;
}

.borderBlocked {
  background: linear-gradient(135deg, transparent 31px, #b2b2b2 0), 0 0,
    no-repeat;
}

@media (max-width: 1130px) {
  .FoodItem:not(:nth-child(3)) {
    margin-right: 0;
  }

  .FoodItem:nth-child(1) {
    margin-right: 40px;
  }
}

@media (max-width: 700px) {
  .FoodItem:nth-child(1) {
    margin-right: 0;
  }
}
